.Home {
  text-align: center;
}

.Home-logo {
  -webkit-animation: Home-logo-spin infinite 20s linear;
          animation: Home-logo-spin infinite 20s linear;
  height: 40vmin;
  position: absolute;
  top:100px;
}

.Home-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Home-link {
  color: #61dafb;
}

.Home-button{
  color: #61dafb;
  cursor: pointer;
  text-decoration:underline;
}

.Home-map{
  width: 100%;
  height: 80vh;
}

.Bottom-container{
  width: 100%;
  height: 20vh;
}

@-webkit-keyframes Home-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Home-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

